
/* 
https://shipow.github.io/searchbox/#tabscss 
*/

.sbx-medium {
  // display: inline-block;
  display: block;
  position: relative;
  // width: 300px;
  width: 100%;
  max-width: 80%;
  height: 37px;
  white-space: nowrap;
  box-sizing: border-box;
  font-size: 1.4rem;
  
}

.sbx-medium__wrapper {
  width: 100%;
  height: 100%;
}

.sbx-medium__input {
  display: inline-block;
  transition: box-shadow .4s ease, background .4s ease;
  border: 0;
  border-radius: 19px;
  box-shadow: inset 0 0 0 1px #D9D9D9;
  background: #FFFFFF;
  padding: 0;
  padding-right: 30px;
  padding-left: 37px;
  // padding: 1em;
  width: 100%;
  height: 100%;
  vertical-align: middle;
  white-space: normal;
  font-size: inherit;
  appearance: none;
}

.sbx-medium__input::-webkit-search-decoration, .sbx-medium__input::-webkit-search-cancel-button, .sbx-medium__input::-webkit-search-results-button, .sbx-medium__input::-webkit-search-results-decoration {
  display: none;
  
}

.sbx-medium__input:hover {
  box-shadow: inset 0 0 0 1px silver;
}

.sbx-medium__input:focus, .sbx-medium__input:active {
  outline: 0;
  box-shadow: inset 0 0 0 1px #AAAAAA;
  background: #FFFFFF;
}

.sbx-medium__input::placeholder {
  color: #AAAAAA;
}

.sbx-medium__submit {
  position: absolute;
  top: 0;
  right: inherit;
  left: 0;
  margin: 0;
  border: 0;
  border-radius: 18px 0 0 18px;
  background-color: rgba(255, 255, 255, 0);
  padding: 0;
  width: 37px;
  height: 100%;
  vertical-align: middle;
  text-align: center;
  font-size: inherit;
  user-select: none;
}

.sbx-medium__submit::before {
  display: inline-block;
  margin-right: -4px;
  height: 100%;
  vertical-align: middle;
  content: '';
}

.sbx-medium__submit:hover, .sbx-medium__submit:active {
  cursor: pointer;
}

.sbx-medium__submit:focus {
  outline: 0;
}

.sbx-medium__submit svg {
  width: 17px;
  height: 17px;
  vertical-align: middle;
  fill: #666666;
}

.sbx-medium__reset {
  display: none;
  position: absolute;
  top: 8px;
  right: 8px;
  margin: 0;
  border: 0;
  background: none;
  cursor: pointer;
  padding: 0;
  font-size: inherit;
  user-select: none;
  fill: rgba(0, 0, 0, 0.5);
}

.sbx-medium__reset:focus {
  outline: 0;
}

.sbx-medium__reset svg {
  display: block;
  margin: 4px;
  width: 13px;
  height: 13px;
}

.sbx-medium__input:valid ~ .sbx-medium__reset {
  display: block;
  animation-name: sbx-reset-in;
  animation-duration: .15s;
}

@keyframes sbx-reset-in {
  0% {
    transform: translate3d(-20%, 0, 0);
    opacity: 0;
  }
  100% {
    transform: none;
    opacity: 1;
  }
}
